import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Ocean from '@/components/Ocean'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Ocean',
      component: Ocean
    }
  ]
})



