let echarts = require('echarts/lib/echarts')

export default {
  name: "radar-chart",
  props: ["name", "name2"],
  data() {
    return {
      myChart: {},
      indicator: [],
      datas: [[], []],
      data: [
        {
          name: '',
          max: 100,
          value: [80, 60]
        },
        {
          name: '',
          max: 80,
          value: [40, 20]
        },
        {
          name: '',
          max: 120,
          value: [80, 60]
        },
        {
          name: '',
          max: 150,
          value: [130, 50]
        },
        {
          name: '',
          max: 50,
          value: [30, 10]
        },
        {
          name: '',
          max: 150,
          value: [80, 130]
        },
        {
          name: '',
          max: 90,
          value: [50, 40]
        },
        {
          name: '',
          max: 110,
          value: [90, 70]
        }
      ]
    }
  },
  mounted() {

    let this_ = this;
    //
    this.dataFormatter();
    this.myChart = echarts.init(document.getElementById('myChart'));
    let option = {
      legend: {
        show: true,
        icon: 'circle',
        right: '2%',
        bottom: '5%',
        orient: 'horizontal',
        itemWidth: 6,
        itemHeight: 6,
        textStyle: {
          fontSize: 10,
          fontWeight: 100,
          color: "#fff"
        },
        data: ['', '']
      },
      radar: {
        center: ["50%", "50%"],
        radius: "58%",
        startAngle: 90,
        splitNumber: 4,
        shape: "circle",
        name: {
          textStyle: {
            color: 'rgba(255,255,255,0.6)',
            fontSize: 12,
            lineHeight: 16,
            rich: {}
          }
        },
        splitArea: {
          areaStyle: {
            "color": ["rgba(0,0,0,.2)"]
          }
        },
        axisLine: {
          show: true,
          lineStyle: {
            color: "rgba(255,255,255,0.3)"
          }
        },
        splitLine: {
          show: true,
          lineStyle: {
            color: "rgba(255,255,255,0.3)"
          }
        },
        indicator: this.indicator
      },
      series: [{
        name: "",
        type: "radar",
        symbol: "circle",
        symbolSize: 5,
        itemStyle: {
          normal: {
            color: 'rgba(74, 212, 179, 1)',
            borderColor: "rgba(74, 212, 179, 0.4)",
            borderWidth: 5
          }
        },
        areaStyle: {
          normal: {
            color: "rgba(74, 212, 179, 0.4)"
          }
        },
        lineStyle: {
          normal: {
            color: "rgba(74, 212, 179, 1)",
            width: 1,
            type: "dotted"
          }
        },
        data: [this.datas[1]]
      }, {
        name: "",
        type: "radar",
        symbol: "circle",
        symbolSize: 5,
        areaStyle: {
          normal: {
            color: "rgba(208, 50, 163, 0.4)"
          }
        },
        itemStyle: {
          color: 'rgba(208, 50, 163, 1)',//
          borderColor: 'rgba(208, 50, 163, 0.4)',//
          borderWidth: 5,//
        },
        lineStyle: {
          normal: {
            type: "dotted",

            color: "rgba(208, 50, 163, 1)",
            width: 1
          }
        },
        data: [this.datas[0]]
      }]
    };
    this.myChart.setOption(option);
    let thisa_ = this
    setTimeout(function () {
      thisa_.myChart.resize()
    },500)
  },
  methods: {
    getdata() {
      this.$axios.get('http://192.168.1.129/api/User/abilities?userId=1000093001647').then((response) => {
        console.log(response)
      }).catch((response) => {
        console.log(response)
      })
    },

    dataFormatter() {
      for (var i = 0; i < this.data.length; i++) {
        var tNameL = this.data[i].name.length % 2 == 0 ? this.data[i].name.length / 2 : this.data[i].name.length / 2 + 1;
        this.indicator.push({
          name: this.data[i].name.substr(0, tNameL) + '\n' + this.data[i].name.substr(tNameL),
          max: this.data[i].max
        })
        this.datas[0].push(this.data[i].value[0]);
        this.datas[1].push(this.data[i].value[1]);
      }
    }
  },
  watch: {
    name: function (ffff, bbbb) {
      console.log('*************')
      let that = this
      setTimeout(function () {
        that.myChart.resize()
      }, 500)

      // this.$nextTick(()=>{
      //   this.myChart.resize()
      // })
    },
  },
}



