
import BarChart from '@/components/demo/BarChart';
import PieChart from '@/components/demo/PieChart';
import RadarChart from '@/components/demo/RadarChart';
import InfoBar from '@/components/demo/InfoBar';
import InfoPie from '@/components/demo/InfoPie';
import InfoRadar from '@/components/demo/InfoRadar';
import Scroller from 'vux/src/components/scroller/index.vue'

import Alert from 'vux/src/components/alert/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
name: "ocean",
data() {
return {
tableData: [],
cardount: '',
currentView: 'InfoBar', // 
eChartsView: 'BarChart',
active: 0,
move: 0,
eCheight: 60, //echarts 
show: true,
show2: false,
name: 'bbbb',
name2:'cccc', // margine
smHight:0,
curHeight: 0,// 
objeChar:'',
objiFor:'',
objJob:'',
ua:'',
inShow:false, //
inShow2:false, // tankuang
moveS:'no',//
tabs: [
{
type: '',
view: 'InfoBar',
view2: 'BarChart',
class1: 'icon-xx'
},
{
type: '',
view: 'InfoPie',
view2: 'PieChart',
class1: 'icon-xx2'
},
{
type: '',
view: 'InfoRadar',
view2: 'RadarChart',
class1: 'icon-xx1'
}
]
}
},
directives: {
TransferDom
},
components: {
BarChart,  //
PieChart,  //
RadarChart, //
InfoBar,
InfoPie,
InfoRadar,
Scroller,
XDialog,
XButton,
Group,
XSwitch,
Alert,
},
mounted() {
var ua = navigator.userAgent.toLowerCase();   
if (/iphone|ipad|ipod/.test(ua)) {
this.ua = 'ios'
$('.activel').css('margin-top','-8px')
console.log($('.activel'))
} else if (/android/.test(ua)) {
this.ua = ''

}

this.getcurHeight()
this.getdata()

this.objeChar = document.getElementById('eCharts')
this.objiFor = document.getElementById('xx1')
this.$nextTick(()=>{
if(document.querySelector('.xxx3')){
console.log('zheshi sha ?')
}
})
},
methods: {
/**
* 
*/
getdata() {
this.$axios.get('http://192.168.1.129/api/Job/special').then((response) => {
this.tableData = response.data.data
console.log(this.tableData)
this.cardount = (this.tableData.length) * 250 + (this.tableData.length) * 10 + (10)  
console.log(this.tableData)
}).catch((response) => {
console.log(response)
})
},
/**
* 
*/
getcurHeight() {
let h = document.documentElement.clientHeight || document.body.clientHeight;
let objeChar = document.getElementById('eCharts')
let objiFor = document.getElementById('xx1')
this.curHeight = h;
console.log(this.curHeight,'gaogoagaoggaoo')
if(this.curHeight<=568){
this.inShow2 = true
this.smHight = 1
console.log('iphone5')
objeChar.style.height='50vh'
objiFor.style.height = 'calc(50vh - 44px)'
}else if(580<=this.curHeight&&this.curHeight<=736){
console.log('iphone6 ')
objeChar.style.height='60vh'
objiFor.style.height = 'calc(40vh - 44px)'
this.smHight = 2
}else if(this.curHeight>736){
console.log('iphoneXXXX')
this.smHight = 0
}
},
/**
* tab
*/

tabChange(i, tab1, tab2) {
if( this.ua === 'ios'){   //ios
setTimeout(function () {
$('.activel').css('margin-top','-8px')
},10)
}
this.active = i
this.currentView = tab1;
this.eChartsView = tab2;

if(this.smHight === 1){ //iphone5

this.objeChar.style.height='50vh';
this.objiFor.style.height = 'calc(50vh - 44px)'
}else if (this.smHight === 2){ //iphone6
this.objeChar.style.height='60vh';
this.objiFor.style.height = 'calc(40vh - 44px)'
}

this.move = 0;
this.show2 = false;
this.name = 'bbbb';
this.name2 = 'cccc'
},
/**
* 
*/
swiperup: function () {
if(this.smHight === 1){
this.objeChar.style.height='30vh';
this.objiFor.style.height = 'calc(40vh - 42px)';
setTimeout(function () {
let xx = document.getElementById('xx3')
this.objJob = document.getElementById('xx3')
this.objJob.style.height = '30vh'
},50)
}else if(this.smHight === 2){
this.objeChar.style.height='40vh';
this.objiFor.style.height = 'calc(30vh - 42px)';
setTimeout(function () {
this.objJob = document.getElementById('xx3')
this.objJob.style.height = '30vh'
},50)
}
this.move = 1;
this.show2 = true;
this.name = 'ffff';
this.name2 = 'eeee';
console.log(this.name2)
},
/**
* 
*/
swiperdown: function() {
console.log('')

if(this.smHight === 1){ //iphone5
this.objeChar.style.height='50vh';
this.objiFor.style.height = 'calc(50vh - 44px)'
}else if (this.smHight === 2){ //iphone6
this.objeChar.style.height='60vh';
this.objiFor.style.height = 'calc(40vh - 44px)'
}

this.move = 0;
this.show2 = false;
this.name = 'bbbb';
this.name2 = 'cccc'
},
onHide () {
console.log('on hide')
},
onShow () {
console.log('on show')
},
},
watch: {
moveS: function (yes, no) {


},
},
}




