          }]
        }],
      // data: [],
      itemNum: 0,
      win: '',
      win2: '',
      colors: ['rgba(209,52,164,1.0)', 'rgba(209,52,164,1.0)', 'rgba(209,52,164,1.0)'],
      gradient: ["rgb(255,255,255)", "rgb(249,232,244)", "rgb(244,209,234)", "rgb(239,187,224)", "rgb(234,164,214)", "rgb(229,142,204)", "rgb(224,119,194)", "rgb(219,97,184)", "rgb(214,74,174)"],
    }
  },
  mounted() {
    let this_ = this;
    //
    // this.initData();
    this.win = $(window).width() > $('#myChart').height() ? $('#myChart').height() : $(window).width();
    console.log(this.win)
    let h = document.documentElement.clientHeight || document.body.clientHeight;
    if (h <= 568) {
      this.win = 238
    } else if (580 <= h && h <= 736) {
      this.win = 334
    } else if (736 < h && h <= 812) {
      this.win = 375
    }
    // this.win2 = this.win
    this.myChart = echarts.init(document.getElementById('myChart'));
    let option = {
      backgroundColor: '',
      series: {
        type: 'sunburst',
        label: {
          color: '#000000',
          rotate: 'tangential'
        },
        itemStyle: {
          borderWidth: 0
        },
        levels: [
          {
            //
          }, {
            // 
            r0: 0,
            r: this.win / 8.8,
            label: {
              fontSize: this.win / 32,
              color: '#fff'
            },
            itemStyle: {
              borderWidth: 1
            }
          }, {
            // 
            r0: this.win / 8.8,
            r: this.win / 4.7,
            itemStyle: {
              color: 'rgba(0,0,0,0)',
              borderWidth: 0
            },
            label: {
              position: 'inside',
              fontSize: this.win / 37

            }
          }, {
            // 
            r0: this.win / 8.8,
            r: this.win / 3.1,
            itemStyle: {
              color: 'rgba(0,0,0,0)'
            },
            label: {
              position: 'inside',
              rotate: 'radial',
              fontWeight: 'bolder',
              fontSize: this.win / 22

            }
          }, {
            // 
            r0: this.win / 8.8,
            r: this.win / 4.0,
            itemStyle: {
              borderWidth: 1
            }
          }, {
            //
            r0: this.win / 3.75,
            r: this.win / 3.25,
            itemStyle: {
              shadowBlur: 1,
              color: 'transparent',
              borderWidth: 0.2
            },
            label: {
              fontSize: this.starSize(),
              color: '#000'
            }
          }, {
            //
            r0: this.win / 3.17,
            r: this.win / 3.15,
            itemStyle: {
              borderWidth: 0
            }
          }, {
            //
            r0: this.win / 2.9,
            itemStyle: {
              borderWidth: 0,
              color: 'rgba(0,0,0,0)'
            },
            label: {
              position: 'inside',
              fontSize: this.win / 44,
              rotate: 'radial',
              fontWeight: '100'
            },
            downplay: {
              label: {
                opacity: 0.5
              }
            }
          }
        ],
        nodeClick: false,
        data: []
      }
    };

    this.myChart.setOption(option);
    this.myChart.showLoading(
      'default',{
      text: '',
      maskColor: 'rgba(0,0,0,0)'
    })
    let _tast = this
    setTimeout(function () {

      // _tast.initData(_tast.datas);
      let xxx = _tast.initData(_tast.datas);
      console.log(xxx)
      _tast.myChart.hideLoading()
      _tast.myChart.setOption({
        series: {
          data: xxx
        }
      })
    }, 2000)

    this.starSize();
    //
    let thisa_ = this
    setTimeout(function () {
      thisa_.win = thisa_.win2
      thisa_.myChart.resize()
      thisa_.starSize()
      console.log(name, 'pie eCharts')
    }, 500)
    // setInterval(function(){ myChart.resize(); }, 500);
  },
  methods: {
    test() {
      this.myChart.resize()
      console.log('211211pppppp')
    },
    starSize() {
      if (this.itemNum > 30) {
        return 6;
      } else {
        return 8;
      }
    },
    initData(data) {
      let that_ = this
      for (var i = 0; i < data.length; i++) {
        //:
          data[i].itemStyle = {
          color: this.colors[i]
        }
        var level1 = data[i].children;
        for (var j = 0; j < level1.length; j++) {
          //opacity:
          var opacity = parseInt(level1[j].itemStyle.opacity);
          var val = opacity;
          var name = level1[j].name;
          level1[j].name = name.substr(0, 2) + '\n' + name.substr(2);
          level1[j].children = [{
            name: val,
            children: [{
              name: '',
              itemStyle: {
                color: that_.gradient[opacity - 1]
              },
              children: level1[j].children
            }]
          }];
          level1[j].children.forEach(function (blook) {
            var block = blook.children[0].children;
            for (var star = 0; star < block.length; star++) {
              //:
              var style = (function (name) {
                switch (name) {
                  case '':
                    // bookScoreId = 3;
                    return that_.gradient[8];
                  case '':
                    // bookScoreId = 2;
                    return that_.gradient[5];
                  case '':
                    // bookScoreId = 1;
                    return that_.gradient[2];
                  case '':
                    // bookScoreId = 0;
                    return that_.gradient[0];
                }
              })(block[star].name);

              block[star].label = {
                color: style,
              };
              that_.itemNum++;
              block[star].itemStyle = {
                shadowColor: that_.gradient[opacity - 1]
              }
              block[star].value = 1;
              var starChild = block[star].children;
              block[star].children = [{
                name: '',
                value: 1,
                itemStyle: {
                  color: that_.gradient[opacity - 1]
                },
                label: {
                  color: that_.gradient[opacity - 1]
                },
                children: starChild
              }]
              block[star].children.forEach(function (book) {
                book.value = 1;
                book.itemStyle = {
                  color: that_.gradient[opacity - 1]
                };
                book.label = {
                  color: that_.gradient[opacity - 1]
                }
                book.children.forEach(function (txt) {
                  txt.value = 1;
                  var tName = txt.name;
                  var tNameL = tName.length % 2 == 0 ? tName.length / 2 : tName.length / 2 + 1;
                  txt.name = tName.substr(0, tNameL) + '\n' + tName.substr(tNameL);
                  txt.label = {
                    color: '#FFFFFF'
                  }
                })
              })
            }
          })
        }

      }
      return data;
    }
  },
  watch: {
    name: function () {
      console.log('*************2')
      let that = this
      setTimeout(function () {
        // that.win =200
        that.myChart.resize()
      }, 500)

      // this.$nextTick(()=>{
      //   this.myChart.resize()
      // })
    },
    name2: function (eeee, cccc) {
      console.log('XXXXXXXXXXX')
    }
  },
}




